import $ from "../core";

$.prototype.modal = function (created) {
	for (let i = 0; i < this.length; i++) {
		const target = $(this[i]).getAttr('data-target')
		$(this[i]).click((e) => {
			e.preventDefault();
			$(target).fadeIn(500);
			document.body.style.paddingRight = window.innerWidth - document.body.offsetWidth + 'px';
			document.querySelectorAll('.fixed-blocks').forEach(e => {
				e.style.paddingRight = window.innerWidth - document.body.offsetWidth + 'px';
			});
			document.body.style.overflow = 'hidden';
		});

		$(`${target} [data-close]`).click(() => {
			$(target).fadeOut(500);
			document.body.style.overflow = '';
			document.body.style.paddingRight = '';
			document.querySelectorAll('.fixed-blocks').forEach(e => {
				e.style.paddingRight = '';
			});
			if (created) {
				setTimeout(() => { document.querySelector(target).remove() }, 501);
			}
		});

		$(target).click(e => {
			if (e.target.classList.contains('modal')) {
				$(target).fadeOut(500);
				document.body.style.overflow = '';
				document.body.style.paddingRight = '';
				document.querySelectorAll('.fixed-blocks').forEach(e => {
					e.style.paddingRight = '';
				});
				if (created) {
					setTimeout(() => { document.querySelector(target).remove() }, 501);
				}
			}
		})
	}
};

$('[data-toggle="modal"]').modal();


$.prototype.createModal = function ({ text, btns } = {}) {
	for (let i = 0; i < this.length; i++) {
		let modal = document.createElement('div');
		modal.classList.add('modal');
		modal.setAttribute('id', $(this[i]).getAttr('data-target').slice(1));

		// btns = {count: num, settings: [[text, classNames={}, close, callback], [...], [...]]}
		const buttons = [];
		const { settings } = btns;
		for (let j = 0; j < btns.count; j++) {
			let btn = document.createElement('button');
			btn.classList.add('btn', ...settings[j][1]);
			btn.textContent = settings[j][0];
			if (settings[j][2]) {
				btn.setAttribute('data-close', 'true');
			}
			if (btns.settings[j][3] && typeof (settings[j][3]) === 'function') {
				btn.addEventListener('click', settings[j][3]);
			}

			buttons.push(btn);
		}

		modal.innerHTML = `
		<div class="modal-dialog">
			<div class="modal-content">
				<button class="close" data-close>
					<span>&times;</span>
				</button>
				<div class="modal-header">
					<div class="modal-title">
						${text.title}
					</div>
				</div>
				<div class="modal-body">
					${text.body}
				</div>
				<div class="modal-footer">
				</div>
			</div>
		</div>
		`;

		modal.querySelector(".modal-footer").append(...buttons);
		document.body.appendChild(modal);
		$(this[i]).modal(true);
		$(this[i].getAttribute('data-target')).fadeIn(500);
	}
};